class Scene3 extends Phaser.Scene {
    constructor() {
        super('opt');
    }
    preload()
    {
        this.load.image("tri_name", "assets/images/name.jpg");
        
        this.load.image("background", "assets/images/background.jpeg");
    } 
    create() {
        this.add.image(400, 300, "background");

        this.add.image(400, 70, "tri_name");

        let style = {font: "40px Arial", fill: '#ffffff', align: "center"};

        //Back Button
        let backButton = this.add.text(0, 560, "back", style)
        .setInteractive()
        .on("pointerover", () => {backButton.setStyle({fill: '#0f0'})})
        .on("pointerout", () => {backButton.setStyle({fill: '#ffffff'})})
        .on("pointerdown", () => {this.scene.start('menu');});
        //Back Button

        //Sound Button
        let song = true;
        let soundButton = this.add.text(320, 450, "sound", {fill:'#0f0', font: "40px Arial", align: "center"})
        .setInteractive()
        .on("pointerover", () => {
            if (song)
            {
                soundButton.setStyle({fill: '#931C22'});
            } else {
                soundButton.setStyle({fill: '#0f0'});
            }
        })
        .on("pointerout", () => {
            if (song)
            {
                soundButton.setStyle({fill: '#0f0'});
            } else {
                soundButton.setStyle({fill: '#931C22'});
            }
        })
        .on("pointerdown", () => {
            if (song)
            {
                soundButton.setStyle({fill: '#931C22'});
                song = false;
            } else {
                soundButton.setStyle({fill: '#0f0'});
                song = true;
            }
        });
        //Sound Button
    }
    update() {}
}